.note {
    color: green;
    background-color: rgb(239, 237, 237);
    border: 2px;
    border-style: solid;
    padding: 10px;
    margin-bottom: 20px;
    font-weight: bold;
    border-radius: 5px;
}

.errorNote {
    color: red;
    background-color: rgb(239, 237, 237);
    border: 2px;
    border-style: solid;
    padding: 10px;
    margin-bottom: 20px;
    font-weight: bold;
    border-radius: 5px;
}